<template>
  <div class="oneScreen" :style="oneScreenStyle">
    <router-view></router-view>
  </div>
</template>
<script>
import { fullScreen } from "@/utils/utils";
export default {
  name: "oneScreen",
  data() {
    return {
      oneScreenStyle: {
        transform: "scale(0)"
      }
    };
  },
  created() {
    this.resizeApp();
    window.addEventListener("resize", this.resizeApp, false);
    window.addEventListener("keyup", this.switchFullScreen, false);
  },
  methods: {
    switchFullScreen(e) {
      if (e.keyCode === 27) {
        // alert("ESC");
        fullScreen();
      }
    },
    resizeApp() {
      let cw = document.documentElement.clientWidth;
      let ch = document.documentElement.clientHeight;
      let mode = localStorage.getItem("screenMode");
      if (!mode) {
        localStorage.setItem("screenMode", "contain");
        mode = "contain";
      }

      if (mode === "contain") {
        this.oneScreenStyle.transform = `scale(${cw / 1920},${ch / 1080})`;
      } else {
        let ratio = 1920 / 1080;

        if (cw / ch >= ratio) {
          // 太宽
          this.oneScreenStyle.transform = `scale(${ch / 1080},${ch / 1080})`;
        } else {
          // 太高
          this.oneScreenStyle.transform = `scale(${cw / 1920},${cw / 1920})`;
        }
      }
    }
  },
  destroyed() {
    window.removeEventListener("keyup", this.switchFullScreen, false);
    window.removeEventListener("resize", this.resizeBox, false);
  }
};
</script>
